.expenseItem {
  margin-inline: 3rem;
  padding: 0.3rem 0.5rem;
  display: flex;
  justify-content: space-between;
  margin: 1rem;
  border-radius: 15px;
}
.expenseObject {
  background-image: linear-gradient(to right, #fc575e, #f7b42c);
}
.incomeObject {
  background-image: linear-gradient(to right, rgb(59, 134, 153), #20bf55);
}

.expenseItem:not(:last-child) {
  border-bottom: 1px solid grey;
}

.expenseItem p {
  font-size: 1.4rem;
  font-weight: 600;
  text-align: center;
  color: rgba(245, 245, 245, 0.774);
}
.expense {
  width: 30%;
}
.total {
  display: inline-block;
  text-align: right;
  font-size: 1.2rem;
  font-weight: 600;
  margin: 1rem 2rem;
  color: teal;
}



ion-icon {
  width: 2rem;
  height: 2rem;
  margin-right: 1rem;
  color: rgb(211, 68, 68);
  cursor: pointer;
}
.delete:hover {
  transform: scale(1.05);
  transition: all 0.2s;
}

.expensesCard {
  max-height: 60vh;
  min-height: 30vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.expensesCard::-webkit-scrollbar {
  width: 12px;
  background-color: transparent;
}

.expensesCard::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-image: linear-gradient(0deg, #bd643a81, #c5501eb4);
}

.sorting {
  height: 1.5rem;
  background-color: wheat;
  border-radius: 15px;
  display: flex;
  justify-content: flex-start;
  margin: 1rem 1.7rem -0.5rem 1rem;
  padding: 0.3rem 0.5rem;
}
.width {
  width: 32%;
  text-align: center;
}

.sort {
  cursor: pointer;
}
